import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule } from '@angular/forms';
import { FlexLayoutModule } from '@angular/flex-layout';

import {
  CardComponent,
  CardActionsComponent,
  CardAvatarComponent,
  CardBodyComponent,
  CardHeaderComponent,
  CardSubtitleComponent,
  CardTitleComponent,
} from './card.component';



@NgModule({
  imports: [
    CommonModule,
    FlexLayoutModule,
  ],
  declarations: [
    CardComponent,
    CardActionsComponent,
    CardAvatarComponent,
    CardBodyComponent,
    CardHeaderComponent,
    CardSubtitleComponent,
    CardTitleComponent
  ],
  exports: [
    CardComponent,
    CardActionsComponent,
    CardAvatarComponent,
    CardBodyComponent,
    CardHeaderComponent,
    CardSubtitleComponent,
    CardTitleComponent
  ]
})
export class CardModule { }

export {
  CardComponent,
  CardActionsComponent,
  CardAvatarComponent,
  CardBodyComponent,
  CardHeaderComponent,
  CardSubtitleComponent,
  CardTitleComponent
} from './card.component';



