import { Component, OnInit, OnDestroy, ChangeDetectionStrategy } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

import { Context } from '../../services/model/context';
import { User } from '../../services/model/user';
import { Subscription } from 'rxjs/Subscription';

import { UserService } from '../../common/user/user.service';
import { TradeService } from '../../common/trade/trade.service';
import { MediaService } from '../../common/media/media.service';
import { VoucherService } from '../../common/voucher/voucher.service';
import { UserActivityService } from '../../common/user/useractivity.service';
import { TranslateService } from '@ngx-translate/core';
import { takeWhile } from 'rxjs/operators';



declare var jquery: any;
declare var $: any;
declare var BoxesFx: any;
@Component({
  selector: 'app-home',
  // changeDetection: ChangeDetectionStrategy.Default,
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit, OnDestroy {
  eventwork: number;
  vouchers: any = [];
  dispensaries: any = [];
  mediaDispensaries: any = [];
  alive = true;
  mobile = false;
  banner: any = [];
  currentPage = 0;
  totalProducts = 0;
  bannerHome = 'banner-index.jpg';

  constructor(private userService: UserService,
    private tradeService: TradeService,
    private mediaService: MediaService,
    private translate: TranslateService,
    private voucherService: VoucherService,
    private useractivityService: UserActivityService, private sanitizer: DomSanitizer) {
    this.eventwork = 1;

  }

  images: Array<any>;
  loadSlider() {
    let welcome = this.translate.instant("WELCOME");;
    let to = this.translate.instant("TO");;
    if (this.mobile) {
      this.banner[0]='banner-index-xs.jpg';
    }else{
      this.banner[0]='banner-index.jpg';

    }
    let bannerimg = this.banner[0];
    this.images = [
      {
        "sType": "div", "styles": { "width": "100%" }, "content": `
        <!--<div class="container-slide" style="background-image: url('/assets/images/${bannerimg}'); width:100%; background-repeat: no-repeat;background-attachment: initial; background-position: center; background-size:cover;">!-->
        <div class="container-slide">
      <div  class="slide-middle-txt animated">
      <h1><span>${welcome} </span><span class="color-txt">${to}</span> </h1><h1> Panda Weed</h1>
  </div>
  </div>
      ` }];
  }



  init() {
    this.getTradesHome();
    this.getVouchersHome();
  }

  ngOnInit() {
    if (window.screen.width <= 959) {
      this.mobile = true;
      this.bannerHome = 'banner-index-xs.jpg';
    }
    this.init();
    this.translate.onLangChange.takeWhile(() => this.alive).subscribe(() => {
      this.loadSlider();
    })
    this.userService.tokenChanged.takeWhile(() => this.alive).subscribe(() => this.init());
    this.userService.tokenValidated.takeWhile(() => this.alive).subscribe(() => { this.init() });
    this.loadSlider();
  }

  onChangeEvent(event: number, selected: string) {
    //JQUERY? FUCK!!
    var $container = $('.albumContainer');
    $container.isotope({
      filter: '*',
      animationOptions: {
        duration: 750,
        easing: 'linear',
        queue: false
      }
    });
    $('.albumFilter li').on('click', function () {
      var selector = $(this).attr('data-filter');
      $container.isotope({
        filter: selector,
        animationOptions: {
          duration: 750,
          easing: 'linear',
          queue: false
        }
      });
      return false;
    });

    this.eventwork = event;

  }

  getTradesHome() {
    this.tradeService.getTrades('4', '4', 1, 10, 1).takeWhile(() => this.alive).subscribe(
      res => {
        for (var i in res) {
          this.dispensaries.push(res[i]);
          if (!res[i] || !res[i].media[0] || !res[i].media[0].mediaUrl) {
            continue;
          }
          this.mediaService.getMedia(res[i].media[0].mediaUrl)
            .takeWhile(() => this.alive).subscribe(result => this.mediaDispensaries.push(this.sanitizer.bypassSecurityTrustResourceUrl(result)));

        }
      });
  }

  splitText(text) {
    if (!text) {
      return text;
    }
    //TODO constant
    if (text.length > 400) {
      return text.substring(0, 500) + "..."
    }
    return text;
  }


  getVouchersHome() {
    this.voucherService.getVouchers(null, '4', this.currentPage, 10, 1).subscribe(
      res => {

        for (var i in res) {
          this.voucherService.getVoucherDetail(res[i].voucherVendorLocationCode).takeWhile(() => this.alive).subscribe(
            res => {
              this.vouchers.push(res);
              if (this.vouchers && this.vouchers[0]) {
                this.totalProducts = this.vouchers[0].totalCount;
              }
            },
            err => {
              console.log('Error occured');
            });
        }
        console.log(this.vouchers);
      },
      err => {
        console.log('Error occured');
      });

  }

  setManageCart(voucherCode: string) {
    console.log(voucherCode);

    //if(Context.token){

    this.useractivityService.manageCart(voucherCode, 1).takeWhile(() => this.alive).subscribe(
      result => {
        console.log(result);
        /*this.useractivityService.getShoppingCart(false).subscribe(
            res => {
              console.log(res);
            },
            error => {
              console.log('Error occured');
            }
          );*/
      },
      error => { console.log('Error occured', error); });

    //}

  }

  ngOnDestroy() {
    this.alive = false;
  }

}



