import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { RouterModule, Routes } from '@angular/router';
import { AgmCoreModule } from '@agm/core';
import { AppComponent } from './modules/app/app.component';
import { FooterComponent } from './modules/app/footer/footer.component';
import { LoginModule } from './modules/login/';
import { SignupModule } from './modules/signup';
import { SignvendorModule } from './modules/signvendor';
import { ForgotModule } from './modules/forgot';
import { HeaderComponent } from './modules/app/header/header.component';
import { ModalModule } from './common/modal/modal.module';
import { AuthModule } from './common/auth';
import { ApiModule } from './common/api';
import { I18nModule } from './common/i18n';
import { MediaModule } from './common/media';
import { TradeModule } from './common/trade';
import { MetadataModule } from './common/metadata';
import { StorageModule } from './common/storage/storage.module';
import { PostModule } from './common/components/post';
import { CardModule } from './common/components/card';
import { TradeCardModule } from './common/components/trade';
import { UserModule } from './common/user';
import { VoucherModule } from './common/voucher';
import { HomeModule } from './modules/home';
import { HomeComponent } from './modules/home/home.component';
import { SliderModule } from './modules/slider';
import { NglModule } from 'ng-lightning/ng-lightning';
import { TranslateModule, TranslateLoader } from "@ngx-translate/core";
import { TranslateHttpLoader } from "@ngx-translate/http-loader";
import { HttpClient } from "@angular/common/http";
import { DateTimePickerModule } from 'ng-pick-datetime';
import { FlexLayoutModule } from '@angular/flex-layout';
import { MatSidenavModule } from '@angular/material/sidenav';
import { StripeCheckoutModule } from 'ng-stripe-checkout';
import { InfiniteScrollModule } from 'ngx-infinite-scroll';

import { NgProgressModule } from '@ngx-progressbar/core';
import { NgProgressHttpModule } from '@ngx-progressbar/http';
import { NgProgressRouterModule } from '@ngx-progressbar/router';
const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'home', loadChildren: './modules/home/index#HomeModule' },
  { path: 'deals', loadChildren: './modules/deals/deals.module#DealsModule' },
  { path: 'profile', loadChildren: './modules/profile/profile.module#ProfileModule' },
  { path: 'social', loadChildren: './modules/social/index#SocialModule' },
  { path: 'dispensaries', loadChildren: './modules/dispensaries/dispensaries.module#DispensariesModule' },
  { path: 'voucher', loadChildren: './modules/voucher/index#VoucherModule' },
  { path: 'about', loadChildren: './modules/about/about.module#AboutModule' },
  { path: 'store', loadChildren: './modules/store/store.module#StoreModule' },
  { path: 'community', loadChildren: './modules/community/community.module#CommunityModule' },
  { path: '**', redirectTo: '/', pathMatch: 'full' }
];
export function HttpLoaderFactory(httpClient: HttpClient) {
  return new TranslateHttpLoader(httpClient, "/assets/i18n/", ".json");
}
@NgModule({
  declarations: [
    AppComponent,
    FooterComponent,
    HeaderComponent,
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routes),
    DateTimePickerModule,
    InfiniteScrollModule,
    AgmCoreModule.forRoot({ apiKey: "AIzaSyDgEOH_ZndVjzvjuNXHDQofj6RP5qb6nOU" }),
    HomeModule,
    FlexLayoutModule,
    MatSidenavModule,
    NgProgressModule.forRoot(),
    NgProgressHttpModule,
    NgProgressRouterModule,
    CardModule,
    PostModule,
    TradeCardModule,
    HttpClientModule,
    SliderModule,
    NglModule.forRoot(),
    UserModule.forRoot(),
    AuthModule.forRoot(),
    StorageModule.forRoot(),
    MediaModule.forRoot(),
    MetadataModule.forRoot(),
    TradeModule.forRoot(),
    ApiModule.forRoot(),
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    }),
    ModalModule,
    LoginModule,
    SignupModule,
    SignvendorModule,
    ForgotModule,
    VoucherModule.forRoot(),
  ],

  bootstrap: [AppComponent]
})
export class AppModule { }



