import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';


import { HomeRoutingModule } from './home-routing.module';
import { HomeComponent } from './home.component';
import { MediaModule } from '../../common/media';
import { AuthModule } from '../../common/auth';
import { I18nModule } from '../../common/i18n';
import { TradeModule } from '../../common/trade';
import { VoucherModule } from '../../common/voucher';
import { SliderModule } from '../slider';
import { TranslateModule, TranslateLoader } from "@ngx-translate/core";
import { NglModule } from 'ng-lightning/ng-lightning';
import { VoucherCardModule } from '../../common/components/voucher';
import { TradeCardModule } from '../../common/components/trade';
import { FlexLayoutModule } from '@angular/flex-layout';
import { CardModule } from '../../common/components/card';

@NgModule({
  imports: [
    CommonModule,
    HomeRoutingModule,
    MediaModule,
    AuthModule,
    VoucherModule,
    TradeModule,
    TranslateModule,
    SliderModule,
    NglModule,
    CardModule,
    VoucherCardModule,
    FlexLayoutModule,
    TradeCardModule,
  ],
  declarations: [HomeComponent]
})
export class HomeModule { }



