import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Headers, RequestOptions } from '@angular/http';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import 'rxjs/add/operator/map';
import { Context } from '../../services/model/context';
import { User } from '../../services/model/user';
import { ApiRequest, ApiBuilder } from '../api';
import { AuthService } from '../auth/';
import { Subject } from "rxjs/Subject";

@Injectable()
export class UserprofileService {

  private changedProfileSource = new Subject<void>();
  public changedProfile = this.changedProfileSource.asObservable();
  
  private requestUser: ApiRequest<any>;

  constructor(private apiBuilder: ApiBuilder, private authService: AuthService) {
    this.requestUser = apiBuilder.defaultOptions()
      .path("user-profile/")
      .build<User>();
  }

  getUserProfile(): Observable<any> {
    return this.requestUser.post("getUserProfile.s",
      {})
      .map(
      res => {
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }

  setUserProfile(dataList: any[]): Observable<any> {
    return this.requestUser.post("setUserProfile.s", dataList)
      .map(
      res => {
        this.changedProfileSource.next();
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }

  changePassword(oldPassword: string, newPassword: string): Observable<any> {
    return this.requestUser.post("changePassword.s",
      {
        oldPassword: oldPassword,
        newPassword: newPassword
      })
      .map(
      res => {
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }

  getUserInfo(): Observable<any> {
    return this.requestUser.post("detail.s",
      {
      })
      .map(
      res => {
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }

  setUserName(newUsername: string): Observable<any> {
    return this.requestUser.post("setusername.s",
      {
        newUsername: newUsername
      })
      .map(
      res => {
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }

  setemail(newEmail: string): Observable<any> {
    return this.requestUser.post("setemail.s",
      {
        newEmail: newEmail
      })
      .map(
      res => {
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }

  getTermsOfUrl(): Observable<any> {
    return this.requestUser.post("termsofuseurl.s",
      {})
      .map(
      res => {
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }

  forgotPassword(emailAddress: string): Observable<any> {
    return this.requestUser.post("forgotpassword.s",
      {
        emailAddress: emailAddress
      })
      .map(
      res => {
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }

  passwordReset(resetCode: string): Observable<any> {
    return this.requestUser.post("password-reset.n",
      {
        resetCode: resetCode
      })
      .map(
      res => {
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }

  deactivate(): Observable<any> {
    return this.requestUser.post("deactivate.s",
      {})
      .map(
      res => {
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }

  addShippingAddress(address: any): Observable<any> {
    console.log('Calling addShippingAddress');
    const headers = new HttpHeaders().set('userAgent', Context.USER_AGENT);
    return this.requestUser.post("shipping-address/add.s",
      {
        fullName: address.fullName,
        country: address.country,
        phoneNumber: address.phoneNumber,
        province: address.province,
        address1: address.address1,
        address2: address.address2,
        city: address.city,
        zipOrPostalCode: address.zipOrPostalCode
      })
      .map(
      res => {
        return res;
      });
  }

  getShippingAddresses(): Observable<any> {
    return this.requestUser.post("shipping-address/all.s",
      {})
      .map(
      res => {
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }

}



