import { Injectable } from '@angular/core';
import { ModalComponent } from './modal.component';
import { Subject } from 'rxjs/Subject';

declare var jquery: any;
declare var $: any;

@Injectable()
export class ModalService {
  private modals: Array<ModalComponent>;
  onCloseModal = new Subject<any>();


  constructor() {
    this.modals = [];
  }

  registerModal(newModal: ModalComponent): void {
    const modal = this.findModal(newModal.modalId);

    // Delete existing to replace the modal
    if (modal) {
      this.modals.splice(this.modals.indexOf(modal));
    }

    this.modals.push(newModal);
  }

  open(modalId: string): void {
    for (const modal of this.modals) {
      this.close(modal.modalId);
    }
    const modal = this.findModal(modalId);
    if (modal) {
      modal.isOpen = true;
      $('#' + modal.modalId + ' .modal').show();
    }
  }

  close(modalId: string, checkBlocking = false): void {
    const modal = this.findModal(modalId);

    if (modal) {
      if (checkBlocking && modal.blocking) {
        return;
      }
      this.onCloseModal.next({ "id": modalId })
      modal.isOpen = false;
    }
  }

  private findModal(modalId: string): ModalComponent {
    for (const modal of this.modals) {
      if (modal.modalId === modalId) {
        return modal;
      }
    }
    return null;
  }
}



