import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Headers, RequestOptions } from '@angular/http';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import 'rxjs/add/operator/map';
import { Context } from '../../services/model/context';
import { Device } from '../../services/model/device';
import { User } from '../../services/model/user';
import { AuthService } from '../auth/';

import { ApiRequest, ApiBuilder } from '../api';


import { Subject } from 'rxjs/Subject';

@Injectable()
export class UserService {

  private toggleLoginSource = new Subject<void>();
  public toggleLogin = this.toggleLoginSource.asObservable();

  private requestUser: ApiRequest<any>;
  private requestDevice: ApiRequest<any>;
  private requestVendor: ApiRequest<any>;
  private requestUserProfile: ApiRequest<any>;

  logueadoChanged = new Subject<boolean>();
  tokenChanged = new Subject<boolean>();
  tokenValidated = new Subject<boolean>();

  signupSuccess = new Subject<boolean>();
  signvendorSuccess = new Subject<boolean>();



  constructor(private apiBuilder: ApiBuilder, private authService: AuthService) {
    this.requestUser = apiBuilder.defaultOptions()
      .path("user/")
      .build<User>();
    this.requestDevice = apiBuilder.defaultOptions()
      .path("device/")
      .build<any>();
    this.requestVendor = apiBuilder.defaultOptions()
      .path("vendor/")
      .build<any>();
    this.requestUserProfile = apiBuilder.defaultOptions()
      .path("user-profile/")
      .build<any>();

  }

  registerDevice(): Observable<any> {
    return this.requestDevice.post("register", {});
  }

  loginAsGuest(): Observable<User> {
    return this.requestUser.post("guest.n", {});
  }

  loginAsUser(userTypeCode: string, userIdentifier: string, token: string): Observable<User> {
    return this.requestUser.post("loginUser.n",
      {
        userTypeCode: userTypeCode,
        userIdentifier: userIdentifier,
        token: token
      })
      .do(
      res => {
        return this.authService.saveLogin(res.firstName, res.lastName, res.roleCode, res.userName, res.token).subscribe(() => {
          this.logueadoChanged.next(true);
          this.tokenChanged.next();
          this.toggleLoginSource.next();
        })
      },
      err => {
        console.log('Error occured');
      }
      );
  }


  getUser(): Observable<User> {
    return this.authService.getUser();
  }
  setLangUser(lang: string): Observable<User> {
    return this.authService.saveLang(lang);
  }
  getUserOldYear(): Observable<User> {
    return this.authService.getUserOldYear();
  }
  setUserYearOld(dataUser: boolean): Observable<boolean> {
    return this.authService.saveUserOldYear(dataUser);
  }

  isLogin() {
    return this.authService.isLoggedIn();
  }

  signUp(userTypeCode: string, userEmail: string, firstName: string, lastName: string,
    userName: string, phoneNumber: string, token: string, dateOfBirth: string): Observable<any> {
    console.log('Calling SignUp', userTypeCode);
    return this.requestUser.post("signUp.s",
      {
        userTypeCode: userTypeCode,
        userEmail: userEmail,
        firstName: firstName,
        lastName: lastName,
        userName: userName,
        phoneNumber: phoneNumber,
        token: token,
        dateOfBirth: dateOfBirth
      });
  }

  signVendor(userTypeCode: string, userEmail: string, firstName: string, lastName: string,
    userName: string, phoneNumber: string, token: string, dateOfBirth: string): Observable<any> {
    console.log('Calling SignUp', userTypeCode);
    return this.requestVendor.post("signUp.s",
      {
        userTypeCode: userTypeCode,
        userEmail: userEmail,
        firstName: firstName,
        lastName: lastName,
        userName: userName,
        phoneNumber: phoneNumber,
        token: token,
        dateOfBirth: dateOfBirth
      });
  }
  saveUserProfile(firstName: string, lastName: string,
    phoneNumber: string, dateOfBirth: number): Observable<any> {
    return this.requestUserProfile.post("setUserProfile.s",
      [{
        key: 'user.billing.firstName',
        value: firstName,
      },
      {
        key: 'user.billing.lastName',
        value: lastName,
      },
      {
        key: 'user.billing.phoneNumber',
        value: phoneNumber,
      },
      {
        key: 'user.billing.dateOfBirth',
        value: dateOfBirth,
      }
      ]
    );
    // return this.requestUserProfile.post("setUserProfile.s",
    //   {
    //     "key": "user_profile",

    //     "value": JSON.stringify([{
    //       key: 'user.profile.firstName',
    //       value: firstName,
    //     },
    //     {
    //       key: 'user.profile.lastName',
    //       value: lastName,
    //     },
    //     {
    //       key: 'user.profile.phoneNumber',
    //       value: phoneNumber,
    //     },
    //     {
    //       key: 'user.profile.dateOfBirth',
    //       value: dateOfBirth,
    //     }
    //     ])
    //   });
  }
  changePassword(oldPassword: string, newPassword: string,
    confirmPassword: string): Observable<any> {
    return this.requestUserProfile.post("changePassword.s",
      {
        oldPassword,
        newPassword,
        confirmPassword,
      });
  }
  forgotPassword(userEmail: string): Observable<any> {
    return this.requestUserProfile.post("forgotpassword.s",
      {
        emailAddress: userEmail,
      });
  }
  getUserProfile(): Observable<any> {
    return this.requestUserProfile.post("getUserProfile.s");
  }
  getUserProfileDetail(): Observable<any> {
    return this.requestUserProfile.post("detail.s");
  }

  logout(): Observable<any> {
    console.log('Calling logout', this.requestUser);
    return this.requestUser.post("logout.s")
      .do(() => this.authService.logout(), () => this.authService.logout())
      .do(() => this.logueadoChanged.next(false), () => this.logueadoChanged.next(false))
      .do(() => true, () => this.toggleLoginSource.next());
  }

  search(querySearchParam: string): Observable<any> {
    console.log('Search Query');
    return this.requestUser.post("search.s",
      { querySearchParam: querySearchParam });
  }
  checkEmail(emailAddress: string): Observable<any> {
    console.log('Search Query');
    const headers = new HttpHeaders().set('userAgent', Context.USER_AGENT);
    return this.requestUser.post("checkemail.n",
      { emailAddress: emailAddress });
  }
}



