import { Component, Input, OnInit, HostListener } from '@angular/core';
import { MediaService } from '../../../common/media/media.service';
import { DomSanitizer } from '@angular/platform-browser';
import { UserActivityService } from '../../user';
@Component({
    selector: 'app-post',
    styleUrls: ['./post.component.css'],
    templateUrl: './post.component.html'
})
export class PostComponent implements OnInit {

    image: any;
    alive = true;

    public avatarDataSquare: any = {
        size: 48, // default size is 100
        //        background: '#F39C12', // by default it will produce dynamic colors
        fontColor: '#FFFFFF',
        border: "2px solid #d3d3d3",
        isSquare: false, // if it is true then letter avatar will be in square defaule value is false
        fixedColor: true //if you enable true then letter will have same color for ever default value is false
    };
    @Input() post: any;
    openedShare: boolean = false;
    size: string;

    constructor(private mediaService: MediaService, private sanitizer: DomSanitizer, private userActivityService: UserActivityService) {
    }

    getImage() {
        if (!this.post || !this.post.media[0] || !this.post.media[0].mediaUrl) {
            return;
        }
        this.mediaService.getMedia(this.post.media[0].mediaUrl).takeWhile(() => this.alive).subscribe(
            result => {
                this.image = this.sanitizer.bypassSecurityTrustResourceUrl(result);
            }
        );
    }
    addLike() {
        this.userActivityService.setFavouritedPost(this.post.storyCode, true).takeWhile(() => this.alive).subscribe(
            result => {
                this.post.favouriteCount++;
            }
        );
    }


    ngOnInit() {
        this.avatarDataSquare.text = this.post.author;
        this.getImage();
    }

    openShare(size?: string) {
        this.size = size;
        this.openedShare = !this.openedShare;
    }
    addComment() {
        if (!this.post.comments) {
            this.post.comments = []
        }
        this.post.comments.push({});

    }

    cancelShare() {
        this.openedShare = false;
    }
    ngOnDestroy(): void {
        this.alive = false;
    }
}



