import { Component, OnInit, OnDestroy, trigger, state, style, transition, animate, HostListener, ElementRef, ViewChild } from '@angular/core';
import { Inject } from "@angular/core";
import { Injectable } from "@angular/core";

import { ModalService } from '../../../common/modal/modal.service'
import { UserService } from '../../../common/user/user.service';
import { AuthService } from '../../../common/auth/auth.service';
import { UserActivityService } from '../../../common/user/useractivity.service';
import { TranslateService } from '@ngx-translate/core';
import { Router } from '@angular/router';
import 'rxjs/add/operator/takeWhile';
import { UserprofileService } from '../../../common/user/userprofile.service';
import { DomSanitizer, DOCUMENT } from '@angular/platform-browser';
import { MediaService } from 'app/common/media/media.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
  animations: [
    trigger('slideInOut', [
      state('in', style({
        transform: 'translate3d(0%, 0, 0)'
      })),
      state('out', style({
        transform: 'translate3d(-100%, 0, 0)'
      })),
      transition('in => out', animate('400ms ease-in-out')),
      transition('out => in', animate('400ms ease-in-out'))
    ]),
  ]
})
@Injectable()
export class HeaderComponent implements OnInit, OnDestroy {


  modalId = 'ModalLogin';
  modalIdjoin = 'ModalJoin';
  modalIdvendor = 'ModalVendor';
  modalIdforgot = 'ModalForgot';
  modalIdOld = 'ModalOld';
  alive = true;
  open = false;
  mobile = false;
  viewMenu = true;
  logobigimg = 'logo_big.png';
  logueado = false;
  emptyCarts = false;
  numberVoucherCart = 0;
  dataPass = false;
  dataPassValue: string = "";
  productsCart;
  nameUser: string = '';
  menuState: string = 'out';
  user: { firstName: string };

  private dom: Document;

  @ViewChild('someVar') taskNoteRef: ElementRef;
  @ViewChild('myTooltip') taskNoteRefTooltip: ElementRef;


  constructor(
    public modalService: ModalService, private userService: UserService,
    private authService: AuthService,
    private translate: TranslateService,
    private router: Router,
    private useractivityService: UserActivityService,
    private userprofileService: UserprofileService,
    private mediaService: MediaService,
    private sanitizer: DomSanitizer,
    @Inject(DOCUMENT) dom: Document
  ) {
    this.dom = dom;
  }

  setLang(lang: string) {
    this.userService.setLangUser(lang).takeWhile(() => this.alive).subscribe(() => {
      this.translate.use(lang);
    })
  }

  setUserOldYear(data: boolean) {
    this.userService.setUserYearOld(data).takeWhile(() => this.alive).subscribe((userData) => {
      console.log(userData);
    })
  }

  getUser() {
    this.logueado = false;
    this.userprofileService.getUserProfile().takeWhile(() => this.alive).subscribe((response) => {
      try {
        this.user = JSON.parse(response.userProfile);
      } catch (error) {

      }

    });
    this.nameUser = "";
    this.userService.getUser().takeWhile(() => this.alive).subscribe((user) => {
      if (user) {
        this.setLang(user.lang || 'en');
        this.nameUser = user.firstName;
      } else {
        this.setLang('en');
      }
      if (user && user.token && !user.guest) {
        this.logueado = true;
      }
      if (this.dataPass) {
        this.modalService.open(this.modalIdforgot);
      }

      // this.userService.getUserOldYear().takeWhile(() => this.alive).subscribe((userData) => {
      //   if (userData) {
      //     console.log('Datos Year OLD '+userData);
      //     //this.setUserYearOld(true);
      //   }else{
      //     console.log('Datos Year OLD '+userData);
      //     this.modalService.open(this.modalIdOld);

      //   }
      // });

    });
  }

  goToProduct(product) {
    this.open = false;
    window.scrollTo(0, 0);
    this.router.navigate(["/voucher/details-voucher/", product.voucherCode]);
  }

  getProductsCart() {

    this.useractivityService.getShoppingCart(false).takeWhile(() => this.alive).subscribe(
      res => {
        this.productsCart = res;
        this.numberVoucherCart = this.productsCart.grandTotalVouchers;

        for (const voucher of this.productsCart.vouchers) {
          if (!voucher || !voucher.media[0] || !voucher.media[0].mediaUrl) {
            return;
          }
          this.mediaService.getMedia(voucher.media[0].mediaUrl).takeWhile(() => this.alive).subscribe(
            result => {
              voucher.image = this.sanitizer.bypassSecurityTrustResourceUrl(result);
            }
          );
        }
      },
      error => {
        this.productsCart = {};
        console.log('Error occured' + error);
      }
    );
  }

  ngOnInit() {
    var passNewUrl = window.location.href;
    if (passNewUrl.indexOf('?') > 0) {
      var passNew = passNewUrl.split("?");
      var passNewDat = passNew[1].split("=");
      console.log(passNewDat[1]);
      this.dataPassValue = passNewDat[1];
      this.dataPass = true;
      // this.onLogout();
      this.authService.logout();
    }
    if (window.screen.width <= 959) {
      this.mobile = true;
      this.logobigimg = 'logo_big-xs.png';
    }
    this.initData();
    this.userService.toggleLogin.takeWhile(() => this.alive).subscribe((data) => {
      this.initData();
    });
    this.userprofileService.changedProfile.takeWhile(() => this.alive).subscribe((data) => {
      this.getUser();
    });
  }

  initData() {
    this.getUser();
    this.getProductsCart();
    this.userService.logueadoChanged.takeWhile(() => this.alive).subscribe(
      (logueado: boolean) => {
        this.getUser();
      }
    );
    this.authService.resolveQuestion.takeWhile(() => this.alive).subscribe(
      (res: boolean) => {
        console.log('respuesta: ' + res);
        if (res) {
          //this.setUserYearOld(true);
          this.modalService.open(this.modalIdOld);
          this.router.navigate(["/"]);
        }
      }
    );

    this.useractivityService.cartChanged.takeWhile(() => this.alive).subscribe(
      (changedCart: boolean) => {
        this.emptyCarts = changedCart;
        if (changedCart) {
          this.getProductsCart();
        }
      }
    );

  }

  onLogout() {
    this.userService.logout().subscribe(
      res => {
        this.router.navigate(["/"]);
        window.location.reload();
      }
    );
  }
  goToCheckout() {
    this.open = false;
    window.scrollTo(0, 0);
    this.router.navigate(["/store/cart"]);
  }

  openLogin() {
    if (this.menuState) {
      this.toggleMenu();
    }
    this.modalService.open(this.modalId);
  }
  openSignup() {
    if (this.menuState) {
      this.toggleMenu();
    }
    this.modalService.open(this.modalIdjoin)
  }

  toggleMenu() {
    // 1-line if statement that toggles the value:
    this.menuState = this.menuState === 'out' ? 'in' : 'out';
    window.scrollTo(0, 0);
  }

  resolveQ(resolved: boolean) {
    if (resolved) {
      this.setUserOldYear(true);
      this.modalService.close(this.modalIdOld);
      this.viewMenu = true;
      this.router.navigate(["/"]);
      // this.userService.getUserOldYear().takeWhile(() => this.alive).subscribe((userData) => {
      //       if (userData) {
      //         this.modalService.open(this.modalIdOld);
      //       }else{
      //         this.setUserOldYear(true);
      //         this.modalService.close(this.modalIdOld);
      //       }
      //     });
    } else {
      this.viewMenu = false;
      this.modalService.close(this.modalIdOld);
      this.router.navigate(["/contact"]);

    }
  }

  closeReset() {
    this.modalService.close(this.modalIdforgot);
    this.router.navigate(["/"]);
    window.location.reload();
  }

  copyToClipboard(dat) {
    this.taskNoteRef.nativeElement.select();
    this.dom.execCommand("Copy");
    this.taskNoteRefTooltip.nativeElement.innerHTML = "Copied: " + dat;
  }

  LeaveMouse() {
    this.taskNoteRefTooltip.nativeElement.innerHTML = "Copy to clipboard";
  }

  ngOnDestroy(): void {
    this.alive = false;
  }

  @HostListener('click', ['$event'])
  onClick(e) {
    console.log(e.target.localName);
    if (e.target.localName === 'div' || e.target.localName === 'li' || e.target.localName === 'ul') {
      this.toggleMenu();
    }
  }

}



