import {Injectable} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import 'rxjs/add/operator/map';
import {Context} from '../../services/model/context';

@Injectable()
export class MediaService {

  constructor(private http: HttpClient) {}

  getMedia(mediaUrl: string): Observable<string> {
    return this.http.get(mediaUrl, {responseType: 'arraybuffer'})
      .map(
      data => {
        return window.URL.createObjectURL(new Blob([data]));
      });

  }

  uploadProfilePicture(pictureBlob: string): Observable<any> {
    const headers = new HttpHeaders().set('userAgent', Context.USER_AGENT);
    return this.http.post<Object[]>(`${Context.API_URL}/fileSystemResource/profilePictureUpload.a`,
      {
        pictureBlob: pictureBlob
      }, {headers})
      .map(
      res => {
        console.log(res);
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }

}



