import { NgModule, ModuleWithProviders } from '@angular/core';
import { UserService } from "./user.service"
import { UserActivityService } from "./useractivity.service"
import { UserprofileService } from "./userprofile.service"
import { ApiModule } from "../api"
import { AuthModule } from "../auth"
import { CommonModule } from '@angular/common';
@NgModule({
    imports: [CommonModule, ApiModule, AuthModule]
})
export class UserModule {
    static forRoot(): ModuleWithProviders {
        return {
            ngModule: UserModule,
            providers: [UserService, UserActivityService, UserprofileService]
        }
    }
}
export { UserService } from "./user.service"
export { UserActivityService } from "./useractivity.service"
export { UserprofileService } from "./userprofile.service"


