import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { ContentSlider, printSlide, SafeHtmlPipe, slideAjaxDiv } from './slider.component';

@NgModule({
    imports: [
        CommonModule
    ],
    declarations: [ContentSlider, printSlide, SafeHtmlPipe, slideAjaxDiv],
    exports: [ContentSlider, printSlide, SafeHtmlPipe, slideAjaxDiv]
})
export class SliderModule { }



