import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Headers, RequestOptions } from '@angular/http';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import 'rxjs/add/operator/map';
import { Context } from '../model/context';

@Injectable()
export class MetadataService {

  constructor(private http: HttpClient) { }

  getVoucherReviews(voucherVendorLocationCode: string): Observable<any> {
    console.log('Calling getVoucherReviews');
    const headers = new HttpHeaders().set('userAgent', Context.USER_AGENT);
    return this.http.post<Object[]>(`${Context.API_URL}/metadata/reviews.s`,
      {
        voucherVendorLocationCode: voucherVendorLocationCode
      }, { headers })
      .map(
      res => {
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }
  getTradeReviews(tradeLocationCode: string): Observable<any> {
    console.log('Calling getVoucherReviews');
    const headers = new HttpHeaders().set('userAgent', Context.USER_AGENT);
    return this.http.post<Object[]>(`${Context.API_URL}/metadata/reviews.s`,
      {
        tradeLocationCode: tradeLocationCode
      }, { headers })
      .map(
      res => {
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }

  createVoucherReview(voucherVendorLocationCode: string, text: string, scoreGiven: number): Observable<any> {
    console.log('Calling createVoucherReviews');
    const headers = new HttpHeaders().set('userAgent', Context.USER_AGENT);
    return this.http.post<Object[]>(`${Context.API_URL}/metadata/create-review.s`,
      {
        voucherVendorLocationCode: voucherVendorLocationCode,
        text: text,
        scoreGiven: scoreGiven
      }, { headers })
      .map(
      res => {
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }

  createTradeReviews(tradeLocationCode: string, text: string, scoreGiven: number): Observable<any> {
    console.log('Calling createTradeReviews');
    const headers = new HttpHeaders().set('userAgent', Context.USER_AGENT);
    return this.http.post<Object[]>(`${Context.API_URL}/metadata/create-review.s`,
      {
        tradeLocationCode: tradeLocationCode,
        text: text,
        scoreGiven: scoreGiven
      }, { headers })
      .map(
      res => {
        return res;
      },
      err => {
        console.log('Error occured');
      }
      );
  }
}



