import { Injectable } from '@angular/core';
import { Context } from '../../services/model/context';
import { User } from '../../services/model/user';
import { Subject } from 'rxjs/Subject';
import { ApiRequest, ApiBuilder } from '../api';
import { AuthService } from '../auth/';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class UserActivityService {

  private requestUser: ApiRequest<any>;
  private requestSource: ApiRequest<any>;

  cartChanged = new Subject<boolean>();

  constructor(private apiBuilder: ApiBuilder, private authService: AuthService) {
    this.requestUser = apiBuilder.defaultOptions()
      .path("user-activity/")
      .build<User>();
    this.requestSource = apiBuilder.defaultOptions()
      .path("purchase/source/")
      .build<User>();
  }

  manageCart(voucherCode: string, amount: number): Observable<any> {
    console.log('Calling manageCart');
    return this.requestUser.post("manageCart.s",
      {
        voucherCode: voucherCode,
        amount: amount
      })
      .map(
        res => {
          this.cartChanged.next(true);
          return res;
        },
        err => {
          console.log('Error occured');
        }
      );
  }
  getShoppingCart(creditUsed: boolean): Observable<any> {
    console.log('Calling getShoppingCart');
    return this.requestUser.post("cart.s",
      {
        creditUsed: creditUsed
      });
  }
  checkout(sourceCode: string, creditUsed: boolean): Observable<any> {
    console.log('Calling checkout');
    return this.requestUser.post("checkout.s",
      {
        sourceCode: sourceCode,
        creditUsed: creditUsed
      });
  }

  setFavouritedTrade(tradeLocationCode: string, favourited: boolean): Observable<any> {
    return this.requestUser.post("favourited.s",
      {
        tradeLocationCode: tradeLocationCode,
        favourited: favourited
      });
  }
  setFavouritedPost(storyCode: string, favourited: boolean): Observable<any> {
    return this.requestUser.post("favourited.s",
      {
        storyCode: storyCode,
        favourited: favourited
      });
  }

  setFavouritedVoucher(tradeLocationCode: string, favourited: boolean): Observable<any> {
    console.log('Calling setFavouritedVoucher');
    return this.requestUser.post("favourited.s",
      {
        tradeLocationCode: tradeLocationCode,
        favourited: favourited
      });
  }
  addSource(sourceHolderName: string, nickname: string, processorToken: string, phoneNumber: string): Observable<any> {
    return this.requestSource.post("add.s",
      {
        "sourceHolderName": sourceHolderName,
        "nickname": nickname,
        "processorToken": processorToken,
        "phoneNumber": phoneNumber
      });
  }
  getSources(): Observable<any> {
    return this.requestSource.get("all.s");
  }


}



