import { Component, OnInit, OnDestroy } from '@angular/core';
import { NgForm, FormGroup, FormControl, Validators } from '@angular/forms';
import { UserService } from '../../common/user/user.service';
import { Context } from '../../services/model/context';
import { User } from '../../services/model/user';
import { ModalService } from '../../common/modal/modal.service';
import { Subscription } from 'rxjs/Subscription';
import { subYears } from 'date-fns';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit, OnDestroy {

  public moment: Date = new Date();
  public autoClose = true;
  public min = new Date(2017, 7, 9);
  public max = subYears(new Date(), 18);
  errorPassword = false;
  errorSignup = false;

  public pickerColor: string = '#0070ba';
  alive = true;
  passwordRegex = new RegExp("^(((?=.*[a-z])(?=.*[A-Z]))|((?=.*[a-z])(?=.*[0-9]))|((?=.*[A-Z])(?=.*[0-9])))(?=.{7,})");

  public es = {
    firstDayOfWeek: 1,
    dayNames: ["domingo", "lunes", "martes", "mircoles", "jueves", "viernes", "sbado"],
    dayNamesShort: ["dom", "lun", "mar", "mi", "jue", "vie", "sb"],
    monthNames: ["enero", "febrero", "marzo", "abril", "mayo", "junio", "julio", "agosto", "septiembre", "octubre", "noviembre", "diciembre"],
    monthNamesShort: ["ene", "feb", "mar", "abr", "may", "jun", "jul", "ago", "sep", "oct", "nov", "dic"]
  };



  viewForm: number;
  user;
  signupForm: FormGroup;
  signupFormTwo: FormGroup;

  subscriptionRegisteredMail: Subscription;
  subscriptionSignUp: Subscription;

  constructor(private userService: UserService, public modalService: ModalService) {
    this.viewForm = 1;
    this.user = {
      firstname: '',
      lastname: '',
      email: '',
      dateOfBirth: '',
      password: '',
      confirmPassword: '',
      phoneNumber: ''
    };
  }

  initData() {
    this.signupForm = new FormGroup({
      'firstname': new FormControl(null, [Validators.required]),
      'lastname': new FormControl(null, [Validators.required]),
      'email': new FormControl(null, [Validators.required], this.registeredMailValidation.bind(this)),
      'dateOfBirth': new FormControl(null, [Validators.required])
    });
    this.signupFormTwo = new FormGroup({
      'phoneNumber': new FormControl(null),
      'password': new FormControl(null, [Validators.required, this.validatePassword.bind(this)]),
      'confirmPassword': new FormControl(null, [Validators.required, this.matchingPasswords.bind(this)])
    });
    this.viewForm = 1;
  }

  ngOnInit() {
    this.initData();
    this.modalService.onCloseModal.takeWhile(() => this.alive).subscribe((modal) => {
      if (modal.id === "ModalJoin") {
        this.initData();
      }
    });
  }

  matchingPasswords(control: FormControl): { [s: string]: boolean } {

    if (control.value !== this.user.password) {
      return { 'matchingPasswords': true }
    }
    return null;
  }

  validatePassword(control: FormControl): { [s: string]: boolean } {
    if (!control.value || !this.passwordRegex.test(control.value)) {
      return { 'password': true }
    }
    return null;
  }

  registeredMailValidation(control: FormControl): Promise<any> {

    return new Promise<any>((resolve, reject) => {
      this.userService.checkEmail(control.value).takeWhile(() => this.alive)
        .subscribe(
        result => {
          console.log(result);
          if (!result.valid) {
            resolve({ 'mailIsRegistered': true });
          } else {
            resolve(null);
          }
        },
        (error) => console.error(`Error: ${error}`));

    });

  }

  onSignup() {

    this.userService.signUp('4', this.user.email, this.user.firstname, this.user.lastname,
      this.user.email, this.user.phoneNumber, this.user.password, this.user.dateOfBirth).takeWhile(() => this.alive).subscribe(
      result => {
        this.userService.signupSuccess.next(this.user);
        this.modalService.close('ModalJoin');
        this.viewForm = 1;
      },
      (error: HttpErrorResponse) => {
        let erro = JSON.parse(error.error)
        if (erro.reasonCode === 456) {
          this.errorPassword = true;
        } else {
          this.errorSignup = true;
        }
        setTimeout(() => {
          this.errorSignup = false;
          this.errorPassword = false;
        }, 3000);


      });
  }

  onViewNextForm(event: number) {
    if (this.moment) {
      this.user.dateOfBirth = this.moment;
    }
    this.viewForm = event;
  }

  ngOnDestroy() {
    this.alive = false;
  }
}



