import { Component, Input, OnInit, HostListener } from '@angular/core';
import { MediaService } from '../../../common/media/media.service';
import { TradeService } from '../../../common/trade/trade.service';
import { OnDestroy } from '@angular/core/src/metadata/lifecycle_hooks';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
    selector: 'app-trade',
    styleUrls: ['./trade.component.css'],
    templateUrl: './trade.component.html'
})
export class TradeCardComponent implements OnInit, OnDestroy {

    @Input()
    trade: any;
    @Input()
    align: string = 'column';
    alive = true;

    image: any;

    constructor(
        private mediaService: MediaService,
        private tradeService: TradeService,
        private sanitizer: DomSanitizer) {
    }

    getImage() {
        if (!this.trade || !this.trade.media[0] || !this.trade.media[0].mediaUrl) {
            return;
        }
        this.mediaService.getMedia(this.trade.media[0].mediaUrl).takeWhile(() => this.alive).subscribe(
            result => {
                this.image = this.sanitizer.bypassSecurityTrustResourceUrl(result);
            }
        );
    }

    loadDetail() {
        this.tradeService.getTrade(this.trade.tradeLocationCode).subscribe((dt) => {
        })
    }

    splitText(text) {
        if (!text) {
            return text;
        }
        //TODO constant
        if (text.length > 100) {
            return text.substring(0, 100) + "..."
        }
        return text;
    }

    ngOnInit() {
        this.getImage();
    }
    ngOnDestroy(): void {
        this.alive = false;
    }
}



