import { Component, OnInit, ContentChild, OnDestroy } from '@angular/core';
import { NgForm, FormGroup, FormControl, Validators } from '@angular/forms';
import { UserService } from '../../common/user/user.service';
import { Context } from '../../services/model/context';

import { ModalService } from '../../common/modal/modal.service';
import { User } from '../../services/model/user';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit, OnDestroy {

  modalIdjoinLogin = 'ModalLogin';
  user;
  signloginForm: FormGroup;
  showPassword = true;
  loginError: boolean;
  alive = true;

  constructor(private userService: UserService, public modalService: ModalService) {
    this.user = {
      username: '',
      password: ''
    };
  }

  initData() {
    this.loginError = false;
    this.showPassword = true;
    this.signloginForm = new FormGroup({
      'username': new FormControl(null, [Validators.required]),
      'password': new FormControl(null, [Validators.required])
    });
  }



  ngOnInit() {
    this.initData();
    this.userService.signupSuccess.takeWhile(() => this.alive).subscribe((n: any) => {
      this.login(n.email, n.password);
    })
    this.modalService.onCloseModal.takeWhile(() => this.alive).subscribe((modal) => {
      if (modal.id === "ModalLogin") {
        this.initData();
      }
    });
  }


  toggleShowPassword() {
    this.showPassword = !this.showPassword;
    var that = this;
    setTimeout(function () {
      that.showPassword = true;
    }, 700);
  }


  onLogin() {
    this.login(this.user.username, this.user.password);
  }
  login(username, password) {
    this.userService.loginAsUser(User.USERTYPECODE, username, password).takeWhile(() => this.alive).subscribe(
      result => {
        this.user = {
          username: '',
          password: ''
        };
        window.location.reload();
        this.modalService.close('ModalLogin');

      },
      error => {
        this.loginError = true;
        if (this.user) {
          this.user.password = '';
        }
        setTimeout(() => {
          this.loginError = false
          console.log("ocultando errror", this.loginError);
        }, 3000);
      });
  }
  ngOnDestroy() {
    this.alive = false;
  }

}



