import { Component, Input, OnInit, HostListener } from '@angular/core';
import { ModalService } from './modal.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-modal',
  styleUrls: ['./modal.scss'],
  template: `
      <div [ngClass]="{'closed': !isOpen}" fxLayoutAlign="center center">
        <div class="modal-overlay" (click)="close(true)"></div>

        <div class="modal" fxLayout="column" fxFlex='40' fxFlex.sm='60' fxFlex.xs='100' [ngClass.xs]="{'left-side-modal': true}" [ngClass.sm]="{'left-side-modal-sm': true}">
          <div class="title" *ngIf="modalTitle">
            <span class="right-align" (click)="close(true)"><img src="assets/images/icon/cancel-20.png"></span>
            <img src="assets/images/fondo-popup.jpg">
            <span *ngIf="modalTitle=='Login'"><h1 class="text-title" [ngClass.xs]="{'text-title-xs': true}" [ngClass.sm]="{'text-title-sm': true}">{{modalTitle}}</h1></span>
            <span *ngIf="modalTitle=='Forgot password'"><h1 class="text-title-forgot" [ngClass.xs]="{'text-title-forgot-xs': true}" [ngClass.sm]="{'text-title-forgot-sm': true}">{{modalTitle}}</h1></span>
            <span *ngIf="modalTitle=='Registration'"><h1 class="text-title-regis" [ngClass.xs]="{'text-title-regis-xs': true}" [ngClass.sm]="{'text-title-regis-sm': true}">{{modalTitle}}</h1></span>
            <span *ngIf="modalTitle=='Question'"><h1 class="text-title-regis" [ngClass.xs]="{'text-title-regis-xs': true}" [ngClass.sm]="{'text-title-regis-sm': true}">{{modalTitle}}</h1></span>
          </div>

          <div class="body" fxLayout="column">
            <ng-content></ng-content>
          </div>
        </div>
      </div>
    `
})
export class ModalComponent implements OnInit {
  @Input() modalId: string;
  @Input() modalTitle: string;
  @Input() blocking = false;
  isOpen = false;

  @HostListener('keyup') onMouseEnter(event) {
    this.keyup(event);
  }

  constructor(private modalService: ModalService, private router: Router) {
  }

  ngOnInit() {
    this.modalService.registerModal(this);
  }

  close(checkBlocking = false): void {
    if (this.modalTitle!='Question') {
        if (this.modalId=='ModalForgot') {
          this.router.navigate(["/"]);
          window.location.reload();
        }else{
          this.modalService.close(this.modalId, checkBlocking);
        }
    }
  }

  private keyup(event: KeyboardEvent): void {
    if (event && event.keyCode === 27) {
      //this.modalService.close(this.modalId, true);
    }
  }
}



