import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { UserActivityService } from '../../../common/user/useractivity.service';
import { UserService } from 'app/common/user/user.service';
import { MediaService } from 'app/common/media/media.service';
import { DomSanitizer } from '@angular/platform-browser';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { ModalService } from '../../modal/modal.service';


@Component({
  selector: 'app-voucher',
  styleUrls: ['./voucher.component.css'],
  templateUrl: './voucher.component.html'
})
export class VoucherCardComponent implements OnInit, OnDestroy {

  @Input()
  voucher: any;
  alive: boolean = true;
  image: any;
  modalId = 'ModalLogin';
  voucherimg = 'default.jpg';
  logueado = false;

  constructor(private useractivityService: UserActivityService,
    private mediaService: MediaService,
    public modalService: ModalService,
    private sanitizer: DomSanitizer,
    private router: Router,
    private userService: UserService) {
    this.userService.tokenChanged.takeWhile(() => this.alive).subscribe(() => this.getImage());
    this.userService.tokenValidated.takeWhile(() => this.alive).subscribe(() => { this.getImage() });
  }

  ngOnInit() {
    if (window.screen.width <= 959) {
      this.voucherimg = 'default-xs.jpg';
    }
    this.getImage();
    this.userService.getUser().takeWhile(() => this.alive).subscribe((user) => {
      if (user && user.token && !user.guest) {
        this.logueado = true;
      }
    });
    
    // console.log("VoucherCardComponent> ",this.voucher);
  }

  getImage() {
    if (!this.voucher || !this.voucher.media[0] || !this.voucher.media[0].mediaUrl) {
      return;
    }
    this.mediaService.getMedia(this.voucher.media[0].mediaUrl).takeWhile(() => this.alive).subscribe(
      result => {
        this.image = this.sanitizer.bypassSecurityTrustResourceUrl(result);
      }
    );
  }

  detailProduct() {
    this.router.navigate(['/voucher/details-voucher', this.voucher.voucherVendorLocationCode]);
  }

  setManageCart(voucherCode: string) {
    if(!this.logueado){
      this.modalService.open(this.modalId);
      return;
    }
    this.useractivityService.manageCart(voucherCode, 1).takeWhile(() => this.alive).subscribe(
      result => {
        console.log(result);
      },
      error => { console.log('Error occured', error); });
  }

  splitText(text) {
    if (!text) {
      return text;
    }
    //TODO constant
    if (text.length > 100) {
      return text.substring(0, 100) + "..."
    }
    return text;
  }

  counter(length) {
    return new Array(length);
  }

  ngOnDestroy() {
    this.alive = false;
  }
}



