import { ApiRequest } from './api.request'
import { Injectable } from '@angular/core';
import { HttpClient, HttpRequest, HttpHeaders } from '@angular/common/http';
import { takeWhile } from 'rxjs/operators';
import { Context } from '../../services/model/context';
@Injectable()
export class ApiBuilder {

    private _path: string
    private _url: string
    private _headers: HttpHeaders;

    constructor(
        private httpClient: HttpClient
    ) { }

    public defaultOptions(): ApiBuilder {
        let api = new ApiBuilder(this.httpClient);
        const he = new HttpHeaders();
        api.headers(he)

        api.url(Context.API_URL)
        return api;
    }

    public path(path: string): ApiBuilder {
        this._path = path;
        return this
    }
    public url(url: string): ApiBuilder {
        this._url = url;
        return this
    }
    public headers(headers: HttpHeaders): ApiBuilder {
        this._headers = headers
        return this
    }

    public build<T>(): ApiRequest<T> {
        return new ApiRequest<T>(this._url, this._path, this.httpClient, this._headers)
    }
}


