
import { StorageService } from "../storage/storage.service"
import { User } from "../../services/model/user"
import { Observable } from 'rxjs/Observable';
import { Injectable, EventEmitter } from '@angular/core';

import { Subject } from 'rxjs/Subject';


const USER_STORAGE_KEY = 'USER_STORAGE';
const USEROLDYEAR_STORAGE_KEY = 'USEROLDYEAR_STORAGE';
const DEVICE_STORAGE_KEY = 'DEVICE_STORAGE';
@Injectable()
export class AuthService {

    tokenExpired = new Subject<boolean>();
    resolveQuestion = new Subject<boolean>();
    isvalidResolveQ = null;

    constructor(private storage: StorageService) {

    }

    ResolveQuestionOld()
    {
            // var dataStorage = localStorage.getItem("pandaweed-app/pandaweeb/USEROLDYEAR_STORAGE") 
        this.isvalidResolveQ = JSON.parse(localStorage.getItem("pandaweed-app/pandaweeb/USEROLDYEAR_STORAGE"));
        if (!this.isvalidResolveQ) {
            this.resolveQuestion.next(true);
        }
    }
    getToken(): Observable<string> {
        return this.storage.get(USER_STORAGE_KEY).map(n => {
            return n ? n.token : n;
        })
    }
    saveDevice(device: string): Observable<User> {
        return this.storage.get(USER_STORAGE_KEY).do(n => { n.device = device; this.saveUser(n) });
    }
    saveToken(token: string): Observable<User> {
        return this.storage.get(USER_STORAGE_KEY).do(n => { n.token = token; this.saveUser(n) });
    }
    saveLang(lang: string): Observable<User> {
        return this.storage.get(USER_STORAGE_KEY).do(n => { if (n) { n.lang = lang; this.saveUser(n) } });
    }
    saveLogin(firstName: string, lastName: string, roleCode: string, userName: string, token: string): Observable<User> {
        return this.storage.get(USER_STORAGE_KEY).do(n => {
            n.firstName = firstName;
            n.lastName = lastName;
            n.roleCode = roleCode;
            n.userName = userName;
            n.token = token;
            n.guest = false;
            console.log("UPDAGIN USER DO ", n);
        }).do((n) => { console.log("SAVE USER LOGIN SAVE", n); this.saveUser(n) });
    }
    getUser(): Observable<User> {
        return this.storage.get(USER_STORAGE_KEY)
    }
    getUserOldYear(): Observable<User> {
        return this.storage.get(USEROLDYEAR_STORAGE_KEY)
    }
    isLoggedIn(): Observable<boolean> {
        return this.storage.get(USER_STORAGE_KEY).map(n => {
            return n && n.token ? true : false;
        }, () => {
            return false;
        })
    }
    isLoggedInAsUser(): Observable<boolean> {
        return this.storage.get(USER_STORAGE_KEY).map(n => {
            return n && n.token && !n.guest ? true : false;
        }, () => {
            return false;
        })
    }
    isLoggedInAsGuest(): Observable<boolean> {
        return this.storage.get(USER_STORAGE_KEY).map(n => {
            return !!n ;
        }, () => {
            return false;
        })
    }
    login(auth: User): Observable<User> {
        return this.storage.set(USER_STORAGE_KEY, auth)
    }
    saveUser(user: User): Observable<User> {
        console.log("USER SAVE ", user);
        return this.storage.set(USER_STORAGE_KEY, user)
    }
    saveUserOldYear(userOldYear: boolean): Observable<boolean> {
        return this.storage.set(USEROLDYEAR_STORAGE_KEY, userOldYear)
    }
    logout() {
        
        return this.storage.remove(USER_STORAGE_KEY)
    }
    destroyUser() {
        return this.storage.clear()
    }






}


