import { Component, OnInit, OnDestroy } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';


import { UserService } from '../../common/user/user.service';
import { User } from '../../common/model/user';
import { AuthService } from '../../common/auth/';
import { I18nService } from '../../common/i18n/';
import { MediaService } from '../../common/media/media.service';
import { TradeService } from '../../common/trade/trade.service';
import { Context } from '../../services/model/context';
import { Observable } from 'rxjs';

import { Subscription } from 'rxjs/Subscription';

declare var jquery: any;
declare var $: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent implements OnInit, OnDestroy {

  title = 'app';
  location = {};

  subscriptionRegisterDevice: Subscription;
  constructor(private userService: UserService, private mediaService: MediaService,
    private tradeService: TradeService, private sanitizer: DomSanitizer, private authService: AuthService) {
  }


  ngOnInit() {
    this.init();
    this.authService.tokenExpired.subscribe(() => {
      this.init();
    });
    this.userService.logueadoChanged.subscribe((loggged) => {
      if (!loggged) {
        this.init();
      }
    });

    //    if (navigator.geolocation) {
    //      navigator.geolocation.getCurrentPosition(position => {
    //        this.location = position.coords;
    //        Context.latitude  = position.coords.latitude;
    //        Context.longitude = position.coords.longitude;
    //        console.log('COORDINATES LATITUDE->' + position.coords.latitude);
    //        console.log('COORDINATES LONGITUDE->' + position.coords.longitude);
    //        this.init();
    //      });
    //    }

  }
  init() {

    let logoutRefrest = () => {
      this.authService.logout().subscribe(() => {
        window.location.reload();
      });
    };

    this.authService.isLoggedIn().subscribe((logged) => {
      console.log("is validated", logged);
      if (logged) {
        this.userService.tokenValidated.next();
        console.log("TOKEN VALIDATEd");
      }
      Observable.of(logged).filter(l => !l).subscribe(() => {
        let user = new User();
        this.authService.saveUser(user).subscribe(() => {
          this.userService.registerDevice().subscribe(d => {
            if (!d || !d.deviceId) {
              logoutRefrest();
              return;
            }
            this.authService.saveDevice(d.deviceId).subscribe((usr) => {
              this.userService.loginAsGuest().subscribe((guest) => {
                if (!guest || !guest.token) {
                  logoutRefrest();
                  return;
                }
                let user = new User();
                this.authService.saveToken(guest.token).subscribe(() => {
                  this.userService.tokenChanged.next();
                }, () => {
                  logoutRefrest();
                });
              }, () => {
                logoutRefrest();
              });
            }, () => {
              logoutRefrest();
            });
          }, () => {
            logoutRefrest();
          }
          );
        }, () => {
          logoutRefrest();
        });
      }, () => {
        logoutRefrest();
      })
    });
  }

  ngOnDestroy() {
    //    if (this.subscriptionRegisterDevice) {
    //      this.subscriptionRegisterDevice.unsubscribe();
    //    }

  }
}



