import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { CardModule } from '../card'
import { TradeCardComponent } from './trade.component';
import { NglModule } from 'ng-lightning/ng-lightning';
import { FlexLayoutModule } from '@angular/flex-layout';
@NgModule({
    imports: [CommonModule, NglModule, FlexLayoutModule, CardModule, RouterModule],
    declarations: [TradeCardComponent],
    exports: [TradeCardComponent]
})
export class TradeCardModule { }



