
import { Observable } from 'rxjs';
import * as localForage from "localforage";
import { Injectable } from '@angular/core';

@Injectable()
export class StorageService {

    private storage;

    constructor() {
        this.storage = localForage.createInstance({
            driver: localForage.LOCALSTORAGE,
            name: 'pandaweed-app',
            version: 1.0,
            storeName: 'pandaweeb',
            description: 'Panda Weed App'
        });
    }

    get(key: string): Observable<any> {
        return Observable.fromPromise(this.storage.getItem(key));
    }

    set(key: string, value: any): Observable<any> {
        return Observable.fromPromise(this.storage.setItem(key, value));
    }

    remove(key: string): Observable<any> {
        return Observable.fromPromise(this.storage.removeItem(key));
    }

    clear(): Observable<any> {
        return Observable.fromPromise(this.storage.clear());
    }

    length(): Observable<number> {
        return Observable.fromPromise(this.storage.length());
    }

    key(index: number): Observable<string> {
        return Observable.fromPromise(this.storage.key(index));
    }

    keys(): Observable<string[]> {
        return Observable.fromPromise(this.storage.keys());
    }
}


