import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Headers, RequestOptions } from '@angular/http';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import 'rxjs/add/operator/map';
import { Context } from '../model/context';
import { ApiRequest, ApiBuilder } from '../api';

@Injectable()
export class VoucherService {

  private request: ApiRequest<any>;
  private requestMetadata: ApiRequest<any>;

  constructor(private apiBuilder: ApiBuilder) {
    this.request = apiBuilder.defaultOptions()
      .path("voucher/")
      .build<any>();
    this.requestMetadata = apiBuilder.defaultOptions()
      .path("metadata/")
      .build<any>();
  }
  getVouchers(categoryCode: string, voucherTypeCode: string, page: number, pageSize: number, sortType: number): Observable<any> {
    console.log('Calling getVouchers');
    return this.request.post("all.s",
      {
        categoryCode: categoryCode,
        voucherTypeCode: voucherTypeCode,
        page: page,
        pageSize: pageSize,
        sortType: sortType
      });
  }
  getCategories(): Observable<any> {
    console.log('Calling getVouchers');
    return this.requestMetadata.post("categories.s",
      {
        categoryTypeCode: "R",
      });
  }
  getVouchersByTradeLocation(categoryCode: string, voucherTypeCode: string, vendorLocationCode:
    string, page: number, pageSize: number, sortType: number): Observable<any> {
    console.log('Calling getVouchersByTradeLocation');
    return this.request.post("all/byLocation.s",
      {
        categoryCode: categoryCode,
        voucherTypeCode: voucherTypeCode,
        vendorLocationCode: vendorLocationCode,
        page: page,
        pageSize: pageSize,
        sortType: sortType
      });
  }
  getVoucherDetail(voucherVendorLocationCode: string): Observable<any> {
    console.log('Calling getVoucherDetail');
    return this.request.post("detail.s",
      {
        voucherVendorLocationCode: voucherVendorLocationCode
      });
  }
  getSales(): Observable<any> {
    console.log('Calling getSales');
    return this.request.post("detail.s");
  }
}



