import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { CardModule } from '../card'
import { VoucherCardComponent } from './voucher.component';
import { NglModule } from 'ng-lightning/ng-lightning';
import { FlexLayoutModule } from '@angular/flex-layout';
@NgModule({
    imports: [CommonModule, NglModule, FlexLayoutModule, CardModule, RouterModule],
    declarations: [VoucherCardComponent],
    exports: [VoucherCardComponent]
})
export class VoucherCardModule { }
 


