import { HttpClient, HttpHeaders } from '@angular/common/http';

export class ApiRequest<T> {
    constructor(
        private url: string,
        private path: string,
        private httpClient: HttpClient,
        private httpHeaders: HttpHeaders
    ) {

    }

    post(subpath: string, data?: any) {
        return this.httpClient.post<T>(this.url + this.path + subpath, data, { headers: this.httpHeaders })
    }
    postFile(subpath: string, data?: any) {
        return this.httpClient.post<T>(this.url + this.path + subpath, data, { headers: this.httpHeaders, reportProgress: true })
    }
    get(subpath?: string) {
        return this.httpClient.get<T>(this.url + this.path + (subpath ? subpath : ''), { headers: this.httpHeaders })
    }

    put(subpath?: string, data?: any) {
        return this.httpClient.put<T>(this.url + this.path + (subpath ? subpath : ''), data, { headers: this.httpHeaders })
    }

}


