import { Component, OnInit, OnDestroy } from '@angular/core';
import { NgForm, FormGroup, FormControl, Validators } from '@angular/forms';
import { UserService } from '../../common/user/user.service';
import { Context } from '../../services/model/context';
import { User } from '../../services/model/user';
import { ModalService } from '../../common/modal/modal.service';
import { Subscription } from 'rxjs/Subscription';
import { subYears } from 'date-fns';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-forgot',
  templateUrl: './forgot.component.html',
  styleUrls: ['./forgot.component.css']
})
export class ForgotComponent implements OnInit, OnDestroy {

  emailSend = false;
  alive = true;
  user;
  forgotForm: FormGroup;

  subscriptionRegisteredMail: Subscription;
  subscriptionSignUp: Subscription;

  constructor(private userService: UserService, public modalService: ModalService) {

  }

  initData() {
    this.emailSend = false;
    this.user = {
      email: '',
    };
    this.forgotForm = new FormGroup({
      'email': new FormControl(null, [Validators.required]),
    });
  }

  ngOnInit() {
    this.initData();
    this.modalService.onCloseModal.takeWhile(() => this.alive).subscribe((modal) => {
      if (modal.id === "ModalForgot") {
        this.initData();
      }
    });
  }

  onSubmit() {
    this.userService.forgotPassword(this.user.email).takeWhile(() => this.alive).subscribe(
      result => {
        this.emailSend = true;
        setTimeout(_ => {
          this.modalService.close("ModalForgot");
        }, 3000);

      },
      (error: HttpErrorResponse) => {
        this.emailSend = true;
        setTimeout(_ => {
          this.modalService.close("ModalForgot");
        }, 3000);
      });
  }

  ngOnDestroy() {
    this.alive = false;
  }
}



