import { Injectable } from '@angular/core';
import { Headers, RequestOptions } from '@angular/http';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Context } from '../../services/model/context';
import { User } from '../../services/model/user';
import { ApiRequest, ApiBuilder } from '../api';
import { Observable } from 'rxjs/Observable';
import { takeWhile } from 'rxjs/operators';

@Injectable()
export class TradeService {

  private request: ApiRequest<any>;
  private requestMetadata: ApiRequest<any>;

  constructor(private apiBuilder: ApiBuilder) {
    this.request = apiBuilder.defaultOptions()
      .path("trade/")
      .build<any>();
    this.requestMetadata = apiBuilder.defaultOptions()
      .path("metadata/")
      .build<any>();

  }

  getTrades(categoryCode: string, categoryTypeCode: string, page: number, pageSize: number, sortType: number, filters?: string[]): Observable<any> {
    let filtersCode = [];
    if (filters && filters.length > 0) {
      for (let filter in filters) {
        filtersCode.push({ filterCode: filters[filter] })
      }
    }
    let opts: any = {
      categoryCode: categoryCode,
      categoryTypeCode: categoryTypeCode,
      page: page,
      pageSize: pageSize,
      sortType: sortType
    };
    if (filtersCode && filtersCode.length > 0) {
      opts.filters = filtersCode;
    }
    return this.request.post("all.s", opts);
  }


  getTrade(tradeLocationCode: string): Observable<any> {
    return this.request.post("detail.s",
      {
        tradeLocationCode: tradeLocationCode
      }
    );
  }
  getTradeReviews(tradeLocationCode: string): Observable<any> {
    return this.requestMetadata.post("reviews.s",
      {
        tradeLocationCode: tradeLocationCode
      }
    );
  }
  createTradeReview(tradeLocationCode: string, text: string, scoreGiven: number): Observable<any> {
    return this.requestMetadata.post("create-review.s",
      {
        tradeLocationCode,
        text,
        scoreGiven
      }
    );
  }
}



